.profile{
  &__user{
    &-img{
      width: 40px;
    }

  }

  &__tab{
    cursor: pointer;
    border-left: 5px solid transparent;
    transition: border-color .2s;
    &.active{
      border-color: $primary !important;
      font-weight: bolder;
    }

    &:hover{
      border-color: $light-gray;
    }

    a{
      display: block;
    }

    &-content{
      display: none;

      &.active{
        display: block;
      }
    }
  }

  &__right{
    width: 30%;
    border-left: $light-border;
  }

  &__form{
    color: $light-black;
    font-size: 0.9rem;
  }
}

@media (max-width: 768px){
  .profile{
    &__right{
      width: 100%;
      border-left: 0;
      border-bottom: $light-border;
    }
  }
}

