.cart-products{
  &__item{
    border-bottom: $light-border;
    padding-bottom: 6px;
    margin-bottom: 6px;
  }

  &__remove{
    color: $primary;
    background-color: $light;
    border: none;
    outline: none;
    width: 20px;
    height: 20px;
    border-radius: 50%;
  }

  &__name{
    font-size: 0.9rem;
    font-weight: bold;
  }

  &__price{
    color: $light-black;
    font-size: $small-text;
  }

  &__img{
    width: 50px;
    height: 50px;
    img{
      object-fit: cover;
      height: 100%;
    }
  }
}