.account {
  width: calc(100% - 40px);
  max-width: 700px;
  margin: auto;
  
  &__login,
  &__register,
  &__forget {
    display: none;
  }

  
  &.account--login {
    .account__login {
      display: block;
    }
  }
  
  &.account--register {
    .account__register {
      display: block;
    }
  }

  &.account--forget {
    .account__forget {
      display: block;
    }
  }
  
  &__tab {
    background-color: #eeeeee;
    color: black;
    cursor: pointer;
  }
  
  &.account--login {
    & .account__tab--login{
      background-color: black;
      color: white;
    }
  }

  &.account--register {
    & .account__tab--register{
      background-color: black;
      color: white;
    }
  }
  
  .login__input-icon {
    width: 50px;
    justify-content: center;
    color: $secondary;
    font-size: 18px;
  }
  
  &__external-text {
    white-space: nowrap;
  }
  
  &__external-header {
    height: 2px;
    background-color: black;
  }
  
  &__external-google {
    background-color: $primary;
  }
  
  &__external-facebook {
    background-color: #4267b2;
  }
  
  &__modal {
    animation-name: modal-enter;
    animation-duration: .3s;

    .modal-dialog {
      width: calc(100% - 30px) !important;
      max-width: 600px !important;
      margin: auto;
    }
  
    .modal-content {
      height: 550px;
    }

    &-forms {
      width: 100%;
    }
  
    .modal__close-btn {
      transform: translate(50%, -50%);
      z-index: 10;
      background-image: none;
      font-size: 1.3rem;
      transition: .3s;
  
      &:hover {
        opacity: 1;
        color: $primary;
      }
    }
  }
}


@media (max-width: 768px) {
  .account__modal-forms{
    width: 100%;
  }
}