.primary-button{
  color: white;
  background-color: $primary;
  border: 0;
  outline: 0;
  transition: .3s;
  padding: 7px 16px;
  border-radius: 3px;
  display: inline-block;
  font-size: $medium-text;
  &:hover{
    background-color: black;
  }

  &--disabled{
    background-color: #5eb8b9;

    &:hover{
      cursor: not-allowed;
      background-color: #5eb8b9;
    }
  }
}