.footer{
  background-color: black;

  &__logo{
    width: 80px;
  }

  &__upper{
    padding: 50px 0;
    border-bottom: 1px solid white;
  }

  &__link{
    cursor: pointer;
    transition: .2s;

    &:hover{
      color: white;
    }
  }
}

@media (max-width: 991px) {
  footer{
    padding-bottom: 60px;
  }
}