.contact{
  &__item{
    padding: 10px;
    border: $light-border;
    border-radius: 10px;

    &-title{
      font-size: 0.9rem ;
    }

    &-text{
      font-size: $small-text;
      color: $light-black;
    }
  }

  &__icon{
    width: 45px;
    height: 45px;
    font-size: 18px;
    border-radius: 50%;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    background-color: $primary;
  }

  &__input{
    border: $light-border;
    background-color: #f2f2f2;
    width: 100%;
    padding: 10px 15px;
    border-radius: 10px;
    outline-color: rgba($color: $primary, $alpha: .5);
    color: $light-black;
    &:focus{
      background-color: white;
    }
  }

  &__map{
    height: 300px;
  }

  textarea{
    min-height: 100px;
  }

  select{
    appearance: none
  }
}

@media (max-width: 768px){
  .contact__side{
    width: 100% !important;
  }
}