.nav {
  padding: 15px 0;
  height: 70px;
  background-color: white;

  &__link {
    white-space: nowrap;
    cursor: pointer;

    a {
      color: $secondary;
      text-decoration: none;
      transition: 0.3s;
      padding: 10px 0;
      display: block;

      &:hover {
        color: $primary;
      }
    }

    &__link-user .nav__link {
      border-bottom: $light-border;

      a {
        padding: 8.4px 12px;
      }
    }

    &-user:hover .nav__user-list {
      visibility: visible;
      opacity: 1;
      transform: translateY(0);
    }

    &-floating-icon {
      @include flexCenter(row);
      @include floatTop(left, true);
      background-color: $primary;
      font-size: $small-text;
      width: 20px;
      height: 20px;
      color: white;
      border-radius: 50%;
    }
  }

  &__logo {
    height: 40px;
  }

  &__user{
    &-list {
      box-shadow: $shadow;
      font-size: $small-text;
      width: 170px;
      opacity: 0;
      visibility: hidden;
      transform: translateY(-10px);
      padding: 10px;
      white-space: nowrap;
      transition: 0.1s;
    }

    &-link{
      a{
        border-bottom: $light-border;
        padding: 8.4px 12px;
      }
    }
  }

  &__search {
    height: 100%;
    position: relative;
    flex-shrink: 1;

    &-input {
      height: 100%;
      padding: 10px;
      padding-right: 40px;
      border: $light-border;
      outline: 0;
    }

    &-icon {
      @include floatTop(right, false);
      height: 100%;
      display: flex;
      align-items: center;
      padding: 0 10px;
      font-size: 1.2rem;
      color: $light-gray;
    }
  }

  &__categories-btn {
    border: $light-border;
    height: 40px;
    width: 40px;
    flex-shrink: 0;
    font-size: 1.2rem;
    button {
      outline: none;
    }
  }

  &__close{
    font-size: $large-text;
  }

  &__side{
    &-logo{
      width: 80px;
    }
    &-link{
      border-bottom: $light-border;
    }
  }
  
  &__cart-btn{
    border: 1px solid $light-black;
    color: $light-black;
  }
  
  &-mobile{
    background-color: white;

    &__list{
      padding: 10px;
    }

    &__link{
      color: $light-black;
      cursor: pointer;

      a{
        color: $light-black;
        cursor: pointer;
      }
    }
  }
}
