@import url('https://fonts.googleapis.com/css2?family=Vazirmatn:wght@200;300;400;500;600;700;800;900&display=swap');

*{ 
  margin: 0;
  padding: 0;
  font-family: Vazirmatn, sans-serif;
}

.text-gray{
  color: $light-gray;
}

body{
  direction: rtl;
}

.section-container{
  max-width: 1300px;
  margin: auto;
  padding: 0 24px;
}

main{
  margin-top: 114px;
}

.page-top{
  height: 200px;
  background-image: url('../images/top-img.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
  position: relative;

  &__overlay{
    background-color: $secondary;
    opacity: .6;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.pagination{
  &__btn{
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $light-black;
    border: $light-border;
    transition: .3s;
    cursor: pointer;

    &:hover{
      border-color: $primary;
    }

    &.active{
      border-color: $primary;
      background-color: $primary;
      color: white;
    }
  }
}

.page-full{
  min-height: calc(100vh - 314px);
}

.form__input{
  padding: 8px;
  width: 100%;
  border: $light-border;
  color: $light-black;
  outline: none;
  border-radius: 5px;
}

.required{
  font-size: 18px;
  color: $primary;
}

.input-caption{
  font-size: $small-text;
  font-style: italic;
}

fieldset {
  margin-bottom: 1em !important;
  border: 1px solid #666 !important;
  padding: 15px 25px;
}

legend {
  padding: 1px 5px !important;
  margin: 0 20px;
  float:none;
  width:auto;
  font-size: $medium-text;
}

.splide__slide{
  &.is-active{
    border-width: 1px !important;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  appearance: textfield;
}

.bg-green{
  background-color: #88feb7;
}

select{
  option{
    padding: 10px 15px;
    &:hover, &:checked{
      background-color: $primary !important;
      color: white;
    }
  }
}

.bg-main{
  background-color: $primary;
}

.text-main{
  background-color: $primary;
}

@media (max-width: 991px) {
  main{
    margin-top: 0;
  }
}

@media (max-width: 768px) {
  html{
    font-size: 14px;
  }
}