.success{
  &-gif{
    width: 200px;
  }
  &__small{
    font-size: $small-text;
  }
  &__details{
    margin-left: 15px;
    margin-bottom: 10px;
    padding-left: 15px;
    border-left: 1px dashed $light-gray;

    > p{
      margin: 0;
    }
  }
  &__table{
    th, td{
      padding: 10px 15px;
    }

    tr{
      border-top: $light-border;
    }
  }
}

@media (max-width: 768px){
  .success{
    &__table{
      th, td{
        display: block;
      }

      tr{
        border-top: $light-border;
      }
    }
  }
}