.favourites{
  td, th{
    padding: 16px;
    text-align: center;
  }

  thead{
    background-color: $primary;
    color: white;
    tr{
      border-color: $primary;
    }
  }

  tr{
    border: $light-border;
  }
  
  &__img{

    img{
      width: 70px;
      height: 70px;
      object-fit: cover;
    }
  }
  
  &__remove{
    background-color: $light;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
  }
}