.shop{
  &__filter{
    width: 320px;
  }

  &__category{
    outline: none;
    cursor: pointer;
  }
}

.filter{
  &__search{
    color: $light-black;

    input{
      padding-left: 30px;
      outline: none;
      border: $light-border;
    }
  }

  &__reset{
    background-color: $primary;
    font-size: 0.8rem;
  }

}

@media (max-width: 991px) {
  .shop__filter{
    width: 100%;
  }
}