
.product{
  &__slider-cont{
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
    max-width: 500px;
  }

  &__details{
    flex-shrink: 1;
  }
}

.single-product{
  &__img{
    height: 450px;
    object-fit: contain;
    text-align: center;
    img{
      height: 100%;
    }
  }

  &__details{
    .product__color{
      .color{
        width: 24px;
        height: 24px;
      }
    }
  }

  &__size{
    border: $light-border;
    color: $light-black;  
    @include flexCenter(row);
    padding: 5px 10px;
    border-radius: 4px;
    cursor: pointer;

    &.active{
      color: $primary;
      border-color: $primary;
      border-width: 2px;
      font-weight: bolder;
    }
  }

  &__quanitity{
    color: $light-black;
    button{
      color: $light-black;
    }
  }

  &__input{
    width: 120px;
    border: $light-border;
    border-radius: 5px;
    outline: 0;
    height: 45px;
  }

  &__favourite{
    color: $light-black;
    cursor: pointer;
  }

  &__categories{
    color: $light-black;
    font-size: $small-text;

    a{
      color: $light-black;
    }
  }

  &__nav{
    height: fit-content !important;
  }

  &__tab{
    color: black;
    background-color: #f7f7f7;
    border: $light-border !important;
    height: 40px !important;

    &:hover{
      color: $light-black;
    }

    &.active{
      background-color: white;
    }
  }
}

.questions{
  &__header{
    button{
      background-color: $secondary !important;
      color: white !important;
      &::after{
        background-image: url('../images/minus-icon.svg') !important;
        background-size: 15px 15px;
      }
      &.collapsed{
        background-color: $light !important;
        color: black !important;
        &::after{
          background-image: url('../images/plus-icon.svg') !important;
        }
      }
    }
  }

  
}

#main-slider, #thumbnail-slider{
  max-width: 400px;
}

@media (max-width: 768px) {
  .product{
    &__slider-cont{
      width: 100% !important;
      margin: auto;
      margin-bottom: 30px;
    }
  }

  #main-slider, #thumbnail-slider{
    max-width: 500px;
  }
}