.product{
  &__img{
    &-cont{
      position: relative;
      img{
        transition: .2s;
        object-position: top;
        object-fit: cover;
        height: 300px !important;
      }
    }
  }

  &__sale{
    background-color: $primary;
    width: fit-content;
    font-size: 0.8rem;
  }
  

  &__favourite{
    width: 30px;
    height: 30px;
  }

  &__price{
    color: green;
    font-size: 0.9rem;
    font-weight: bold;
    &--old{
      color: gray;
      text-decoration: line-through;
    }
  }

  &__author{
    color: rgb(128, 128, 128);
    font-size: 0.9rem;
    margin: 5px 0;
  }
}

.hero__carousel .owl-stage-outer{ 
  border: $light-border !important;
  border-radius: 10px;
}